.color2 {color: #0f1e44;}
.color3 {color: #1250a0;}
.color4 {color: #3783ff;}
.color5 {color: #6bc0fe;}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #140f07;
    color: #6bc0fe;
}

.nav {
    background-color: #140f07;
    box-shadow: 0px 8px 16px 4px rgba(0, 0, 0, 0.4);
    padding: 12px 24px;
    text-align: center;
    color: #6bc0fe;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav h1 {
    font-size: 3rem;
}

.container {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 40px 20px;
}

.form {
    width: 25%;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 10px;
}

.form-group input, .form-group select {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.form-group button {
    background-color: #1250a0;
    color: #fff;
    padding: 10px 20px;
    border: none;
    width: 100%;
    border: 2px solid #1250a0;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.form-group button:hover {
    background-color: transparent;
    color: #1250a0;
}

footer {
    width: 100%;
    background-color: #140f07;
    box-shadow: 0px -8px 16px 4px rgba(0, 0, 0, 0.4);
    padding: 12px 24px;
    text-align: left;
    color: white;
}

footer h1 {
    font-size: 2rem;
}

#botao {
    padding: 10px;
    background-color: #1250a0;
    border: 0;
    cursor: pointer;
    margin-left: 20px;
    transition: all 0.3s ease;
    border-radius: 50%;
}

#botao:hover {
    background-color: #3783ff;
}

.list {
    display: flex;
    margin-top: 50px;
    justify-content: center;
    background-color: #140f07;
    flex-direction: column;
    text-align: center;
    width: 1000px;
}

#header {
    padding: 10px;
    color: #6bc0fe;
    font-size: 26px;
    font-family: sans-serif;
    display: flex;
    justify-content: space-between;
}
#header button {
    background-color: #1250a0;
}
#container {
    width: 770px;
}

#calendar {
    width: 100%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
}
.day {
    width: 100px;
    padding: 10px;
    height: 150px;
    cursor: pointer;
    box-sizing: border-box;
    background-color: #3783ff;
    margin: 5px;
    box-shadow: 0px 0px 3px #0f1e44;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 15%;
    font-size: 18px;
    color: #140f07;
}
.day:hover {
    background-color: #6bc0fe;
}

.day + #currentDay {
    background-color: #1250a0;
}
.event {
    font-size: 12px;
    padding: 3px;
    background-color: #0f1e44;
    color: white;
    border-radius: 5px;
    max-height: 55px;
    overflow: hidden;
}
.padding {
    cursor: default !important;
    background-color: #140f07 !important;
    box-shadow: none !important;
}

.calendarButton {
    padding: 10px;
    margin: 10px;
    background-color: #1250a0;
    color: #6bc0fe;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
p {
    text-align: center;
}

/* Estilos para telas pequenas (até 600px de largura) */
@media only screen and (max-width: 600px) {
    .container {
        padding: 20px;
    }
    .form {
        width: 100%;
    }
}

/* Estilos para telas médias (de 601px a 900px de largura) */
@media only screen and (min-width: 601px) and (max-width: 900px) {
    .container {
        padding: 40px;
    }
}

/* Estilos para telas grandes (acima de 900px de largura) */
@media only screen and (min-width: 901px) {
    .container {
        padding: 60px;
    }
}

/* Estilos para o calendário em telas pequenas */
@media only screen and (max-width: 600px) {
    .day {
        width: 80px;
        height: 120px;
        font-size: 14px;
    }
}

/* Estilos para o calendário em telas médias e grandes */
@media only screen and (min-width: 601px) {
    .day {
        width: 100px;
        height: 150px;
        font-size: 18px;
    }
}
section#menu {
    display: none;
    text-align: center;
    background-color: white;
    height: 500px;
    width: 500px;
    border-radius: 7px;
    margin: auto; 
    padding: 20px; position: absolute; top: 100px;
    left: 50%;
    margin-left: -250px;
}

h1 {
    font: normal arial;
    font-size: 36px;
}

p {
    text-align: left;
    color: black;
}

h3 {
    text-align: left;
}
